import React, { useState } from 'react';
import Item from './Item';
import { nameList } from '../constants';
import './style.css';

const FilterPerson = () => {
  const [name, setName] = useState('');
  const [isFilterOpen, setFilter] = useState(false);
  const [filterName, setFilterName] = useState('');
  const filterList = nameList.filter(item => item.name.includes(filterName));
  const list = filterList.map(item => (
    <Item 
      key={item.id}
      colorPoint={item.color}
      name={item.name}
      setName={setName}
      setFilter={setFilter}
    />
  ))
  
  const handleClickFilter = () => {
    setFilter(!isFilterOpen);
  } 

  const handleChangeInputFolter = e => {
    setFilterName(e.target.value);
  }

  return (
    <div className="wrapper">
		{isFilterOpen ? <input className="inputFilter" placeholder="Введите имя" value={filterName} onChange={handleChangeInputFolter}/>:<div className="filter" onClick={handleClickFilter}>{name ? name : 'Filter by people'}</div>}
    {
      isFilterOpen && <div className="filter-inner">
        {list}
      </div>
    }
	</div>
  );
};

export default FilterPerson;
