import React from 'react';
import { render } from 'react-dom';
import {
  HashRouter,
  Switch,
  Route,
} from 'react-router-dom';
import './index.css';
import App from './App';
import Gallery from './Gallery';
import CreateMemePage1 from './CreateMemePage1';
import CreateMemePage2 from './CreateMemePage2';
import CreateMemePage3 from './CreateMemePage3';

render(
  <HashRouter>
    <Switch>
      <Route
        path="/"
        component={App}
        exact
      />
      <Route
        path="/gallery"
        component={Gallery}
        exact
      />
      <Route
        path="/create-meme-page1"
        component={CreateMemePage1}
        exact
      />
      <Route
        path="/create-meme-page2"
        component={CreateMemePage2}
        exact
      />
      <Route
        path="/create-meme-page3"
        component={CreateMemePage3}
        exact
      />
    </Switch>
  </HashRouter>,
  document.getElementById('root'),
);
