import React from 'react';
import Pattern from './Pattern';
import iconBtnAdd from '../img/add-button-icon.svg';
import { memeList } from '../constants';
import './style.css';

const PatternList = ({ setAddPattern, history }) => {
  const handleAddPatternClick = () => {
    setAddPattern(true);
  }
  const list = memeList.map(item => (
    <Pattern 
      key={item.id}
      history={history}
    />
  ));
  return (
    <div className="patternList">
      {list}
      <div className="plus">
        <div className="plusWrapper" style={{ backgroundImage: `URL("${iconBtnAdd}")` }} onClick={handleAddPatternClick}></div>
        <div className='hid'></div>
      </div>
    </div>
  );
}

export default PatternList;
