import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import domtoimage from 'dom-to-image';
import Header from '../Header';
import Button from '../Button';
import StepsChainCreateMeme from '../StepsChainCreateMeme';
import patternMeme from '../img/pen.png';
// import SB from '../img/sponge.jpg';
import iconSave from '../img/save-icon.svg';
import './style.css';

class CreateMemePage2 extends Component {
  state = {
    text: '',
  }

  changeText = (event) => {
    this.setState({
      text: event.currentTarget.value
    });
  }

  handleClick = () => {
    const node = document.getElementById("picMeme");
    console.log(node);

    domtoimage.toPng(node)
      .then(function (dataUrl) {
          var img = new Image();
          img.src = dataUrl;
          document.body.appendChild(img);
      })
      .catch(function (error) {
          console.error('oops, something went wrong!', error);
    });
  }

  render() {
    const { text } = this.state;
    return (
      <div className="createMemePage">
        <Header />
        <div className='createMemePage2Content'>
          <div className="createMemeBlock2">
            <div className="picMeme2" style={{ backgroundImage: `URL("${patternMeme}")` }}>
              {/* <div className='text'>ihihihhijjikh</div> */}
            </div>
            <div className="btnSaveWrapper">
            <Link to={'/create-meme-page3'} className='link'>
              <Button
                textButton='Save'
                icon={iconSave}
              />
            </Link>
          </div>
          </div>
          {/* <label>
            <input type="text" className="toptext" placeholder="Add text to the top" onChange={this.changeText} value={text} />
          </label> */}
          {/* <div onClick={this.handleClick}>кликни</div> */}
        </div>
        <StepsChainCreateMeme
          btnPage1={true}
          btnPage2={true}
          btnPage3={false}
        />
      </div>
    );
  }
}

export default CreateMemePage2;
