import React, { useState } from 'react';
import Header from '../Header';
import FilterPerson from '../FilterPerson';
import StepsChainCreateMeme from '../StepsChainCreateMeme';
import PatternList from '../PatternList';
import AddPattern from '../AddPattern';
import './style.css';

const CreateMemePage1 = ({ history }) => {
  const [isAddPattern, setAddPattern] = useState('');
  return (
    <div className="CreateMemePage">
      <Header />
      <div className='mainContent'>
        <div className='filterPeople'>
        <FilterPerson />
        </div>
        <PatternList history={history} setAddPattern={setAddPattern} />
        <StepsChainCreateMeme 
          btnPage1={true}
          btnPage2={false}
          btnPage3={false}
        />
      </div>
      {isAddPattern && <AddPattern />}
    </div>
  );
}

export default CreateMemePage1;
