import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../Header';
import Button from '../Button';
import StepsChainCreateMeme from '../StepsChainCreateMeme';
import patternMeme from '../img/pen.png';
import iconSlack from '../img/slack-icon.svg';
import './style.css';

class CreateMemePage3 extends Component {
  render() {
    return (
      <div className="createMemePage3">
        <Header />
        <div className='createMemePage3Content'>
          <div className="createMemeBlock2">
            <div className="picMeme2" style={{ backgroundImage: `URL("${patternMeme}")` }} />
            <div className="btnSaveWrapper">
              <Link to={'/create-meme-page3'} className='link'>
                <Button
                  textButton='Post in #random'
                  icon={iconSlack}
                />
              </Link>
            </div>
          </div>
          <StepsChainCreateMeme
            btnPage1={true}
            btnPage2={true}
            btnPage3={true}
          />
        </div>
      </div>
    );
  }
  
}

export default CreateMemePage3;
