import React from 'react';
import PropTypes from 'prop-types';
import iconSpy from '../img/spy-icon.svg';
import classNames from 'classnames';
import iconDance from '../img/dance-icon.svg';
import iconLol from '../img/lol-icon.svg';
import iconTriangle from '../img/triangle-icon.svg';
import './style.css';

const StepsChainCreateMeme = ({ btnPage1, btnPage2, btnPage3 }) => {
  const stepOneClass = classNames(
    'stepCreateMeme',
    { 'stepTaken': btnPage1 }
  )
  const stepTwoClass = classNames(
    'stepCreateMeme',
    { 'stepTaken': btnPage2 }
  )
  const stepTreeClass = classNames(
    'stepCreateMeme',
    { 'stepTaken': btnPage3 }
  )
  return (
    <div className='stepsCreateMeme'> 
      <div className={stepOneClass}>
        <div
          className='iconStep'
          style={{ backgroundImage: `URL("${iconSpy}")` }}
        />
        <span className='textStep'>Choose person</span>
      </div> 
      <div className='triangleIcon' style={{ backgroundImage: `URL("${iconTriangle}")` }} />
      <div className={stepTwoClass}>
        <div
          className='iconStep'
          style={{ backgroundImage: `URL("${iconDance}")` }}
        />
        <span className='textStep'>Create memes</span>
      </div> 
      <div className='triangleIcon' style={{ backgroundImage: `URL("${iconTriangle}")` }} />
      <div className={stepTreeClass}>
        <div
          className='iconStep'
          style={{ backgroundImage: `URL("${iconLol}")` }}
        />
        <span className='textStep'>Share memes</span>
      </div> 
  </div>
  );
};

StepsChainCreateMeme.propTypes = {
  btnPage1: PropTypes.bool.isRequired,
  btnPage2: PropTypes.bool.isRequired,
  btnPage3: PropTypes.bool.isRequired,
};

export default StepsChainCreateMeme;
