import React from 'react';
import './style.css';

const Item = ({ colorPoint, name, setName, setFilter }) => {
  const handleClickName = () => {
    setName(name);
    setFilter(false);
  }

  return (
			<div className="itemFilter" onClick={handleClickName}>
        <div className="point" style={{ backgroundColor: `${colorPoint}`}} />
        <div className="name">{name}</div>
      </div>
  );
};

export default Item;
