import React, { Component } from 'react';
import pattern from '../../img/pen.png';
import './style.css';

class Pattern extends Component {
  handleClickPattern = () => {
    const { history } = this.props;
    history.push('/create-meme-page2');
  }

  render() {
    return (
      <div className="pattern" style={{ backgroundImage: `url("${pattern}")`}} onClick={this.handleClickPattern}></div>
    );
  }
}

export default Pattern;
